<script>
import TXNNote from './TXNNote.svelte'
import TXNColor from './TXNColor.svelte'
import TXNError from './TXNError.svelte'

import { onMount, onDestroy } from 'svelte'
import { getCookie, SLAPIRequest } from '../../utils/api'
import { dcs } from '../../maps/dcs.js'
import { txnStatuses } from '../../maps/txnStatuses'
import { txnGroups } from '../../maps/txnGroups'
import { addIDHover, addNoteHover, removeIDHover, removeNoteHover } from '../../utils/tooltips.js'

export let txn

const cls = ''
const keys = Object.keys(dcs)
const vals = Object.values(dcs)

const status = txn.transactionStatusId && txnStatuses[txn.transactionStatusId] ? txnStatuses[txn.transactionStatusId].name : null

let overdueColor = ''
let secondsOverdue = ''
let deadlineColor = ''
let deadlineDate = ''
let noteIcon
let init = true
let idDiv
let errors = []
const errLimit = txn.currentTransactionErrorCount > 500 ? 500 : txn.currentTransactionErrorCount

onMount(async () => {
  if (init) {
    init = false
    if (txn.notes.length > 0) addNoteHover(noteIcon)
    if (txn.currentTransactionErrorCount > 0) {
      errors = await getErrorsList()  
      addIDHover(idDiv)
    }
  }
})

onDestroy(() => {
  if (txn.notes.length > 0) removeNoteHover(noteIcon)
  if (txn.currentTransactionErrorCount > 0) removeIDHover(idDiv)
})

const getErrorsList = async () => {
  const apiReq = new SLAPIRequest({
    service: 'SoftLayer_Provisioning_Version1_Transaction',
    func: `${txn.id}/getCurrentTransactionErrors`,
    username: await getCookie('ims_int_portal_login'),
    password: await getCookie('ims_int_portal_hash')
  })

  const pages = Math.ceil(txn.currentTransactionErrorCount / 100)

  apiReq.limit(100)
  const err = await apiReq.getNumOfPages(pages > 5 ? 5 : pages, false)

  return err
}
</script>

<tr style="height:22px;" class="{cls}">
  <td>
    <div style="position: relative; width: 120px;">
      <!-- ERRORS -->
      <a bind:this={idDiv} target="_blank" href="https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/{txn.id}/1" class="red">{txn.id}{#if txn.currentTransactionErrorCount > 0} <img src="img/error.svg" alt="error" width="16" height="16">{/if}</a>
      {#if txn.currentTransactionErrorCount > 0}
        <div class="errorEl" style="display: none; right: 0px; top: 0px;">
          <table cellpadding="1" class="helperTable" cellspacing="0">
            <caption>
              <div style="width: 50%; float: left; text-align: left; font-weight: bold;">Current errors for HWTXN #{txn.id} | {txn.currentTransactionErrorCount} errors for this txn, displaying up to {errLimit}</div>
              <div style="width: 50%; float: right; text-align: right; font-weight: bold;">{txn.guest ? txn.guest.hostname : txn.hardware ? txn.hardware.hostname : ''}</div>
            </caption>

            <thead>
              <tr>
                <th width="100px">Date</th>
                <th>Script Name</th>
                <th width="160px">Status</th>
              </tr>
            </thead>

            <tbody>
            {#each errors as error}
              <TXNError {error}></TXNError>
            {/each}
            </tbody>
          </table>
        </div>
      {/if}

      <!-- NOTES -->
      <a target="_blank" href="https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/{txn.id}/3" bind:this={noteIcon}>
        <img src="img/icons/note_add{txn.notes.length === 0 ? '_grey' : ''}.png" width="16" height="16" border="0" alt="note">
      </a>

      {#if txn.notes.length > 0}
        <div id="last_note_div_api_{txn.id}" style="display: none; position: absolute; left: 0px; top: 0px; z-index: 5; border-style: solid; border-width: 2px; border-color: #b72e33; clear: both; width: 650px; background-color: #ffffff; padding: 5px;">
          <table cellpadding="1" class="helperTable" cellspacing="0">
            <caption>
              <div style="width: 50%; float: left; text-align: left; font-weight: bold;">Notes for HWTXN #{txn.id}</div>
              <div style="width: 50%; float: right; text-align: right; font-weight: bold;">{txn.guest ? txn.guest.hostname : txn.hardware ? txn.hardware.hostname : ''}</div>
            </caption>

            <thead>
              <tr>
                <th width="100px">Date</th>
                <th width="80px">Employee</th>
                <th>Message</th>
                <th width="160px">Status</th>
              </tr>
            </thead>

            <tbody>
            {#each txn.notes as note}
              <TXNNote note={note}></TXNNote>
            {/each}
            </tbody>
          </table>
        </div>
      {/if}
      
    </div>
  </td>

  <!-- HOSTNAME && LOCATION -->
  {#if txn.guest}
    <td><a target="_blank" href="https://internal.softlayer.com/Virtual/edit/{txn.guest.id}" class="red">{txn.guest.hostname}</a> - <a target="_blank" href="https://internal.softlayer.com/Virtual/osConfiguration/{txn.guest.id}" class="red">Configs</a></td>
    <td>{txn.guest.locationPathString || ''}</td>
  {:else if txn.hardware}
    <td><a target="_blank" href="https://internal.softlayer.com/Hardware/update/{txn.hardware.id}" class="red">{txn.hardware.serialNumber}</a> - <a target="_blank" href="https://internal.softlayer.com/Hardware/osConfiguration/{txn.hardware.id}" class="red">Configs</a></td>
    <td>{txn.hardware.locationPathString || ''}</td>
  {:else}
    <td><a target="_blank" href="https://internal.softlayer.com/Hardware/buildProvision/{txn.id}" class="red">Not assigned</a></td>
    <td>{keys[vals.indexOf(txn.locationId)]}</td>
  {/if}
  
  <td><a target="_blank" href="https://internal.softlayer.com/HardwareTransaction/groupDetails/{txn.transactionGroupId}" class="red">{txnGroups[txn.transactionGroupId]}</a></td>
  
  <!-- STATUS -->
  {#if status && txn.processInstance}
    <td>Process instance <a class="red" target="_blank" href="https://internal.softlayer.com/ProcessInstance/viewProcessInstance/{txn.processInstance.id}">{txn.processInstance.id}</a></td>
  {:else if status}
    <td><a href="http://wiki.softlayer.local/wiki/index.php/{status}" target="wiki"><img src="img/question_icon.jpg" width="10" height="10" border="0" alt="Question mark"></a><a target="_blank" href="https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/{txn.id}/4" class="red"> {status}</a></td>
  {:else}
    <td></td>
  {/if}

  <td></td>
  
  <!-- COLORS -->
  <TXNColor txn={txn}></TXNColor>
</tr>