<script>
import { onMount, onDestroy, createEventDispatcher  } from 'svelte'
import { localImages, resolveIMSURL, strToHTML } from '../utils/str.js'
import { removeTicketSearchForm, notifyYellowTicketsLegacy } from '../utils/tickets.js'
import { BDSParser, getInitialBDSPage } from '../utils/bds.js'
import { removeScripts } from '../utils/html.js'
import { urlPartFormat } from '../utils/arr.js'
import { formatDate } from '../utils/date.js'
import { addHoverHandlers } from '../utils/tooltips.js'

export let queue

const dispatch = createEventDispatcher()
const titleClick = () => dispatch('titleClick', true)

let interval;
let bodySrc
let lastUpdated = 'init'
let lastUpdateStyle = ''

// Main updater function - Will fetch the generic queue, strip any funkiness
// then replace the bodySrc outerHTML with the innerHTML of the result
const updater = async () => {
  lastUpdateStyle = 'opacity: 0.5'
  let urlSrc = queue.url

  let htmlRes = await getInitialBDSPage(urlSrc)
  htmlRes = removeScripts(htmlRes)

  // const el = document.createElement('div')
  bodySrc.innerHTML = htmlRes.outerHTML
  
  lastUpdated = formatDate()
  lastUpdateStyle = 'opacity: 1'
}

const freq = queue.freq > 0 ? queue.freq*30*1000 : 0

onMount(() => {
  updater()
  if (freq !== 0) setInterval(updater, freq)
})

onDestroy(() => {
  clearInterval(updater)
})
</script>

<div class="card-header">
  <span class="h6" on:click={titleClick}>{queue.title}</span>
  <span class="h6" style={lastUpdateStyle} on:click={updater}>{lastUpdated}</span>
</div>
<div class="card-body" bind:this={bodySrc}></div>
