/**
 * @type {[{top: number, right: number, bottom: number, left: number, el: HTMLElement}]}
 */
let coords = []

/**
 * Make elements resizable
 * @param {HTMLElement} el Element to change
 */
export function makeResizableDiv(el) {
  /**
   * Utility function to get the general bounding boxes of the origin element
   */
  function getElPos () {
    const {top, left, right, bottom} = el.getBoundingClientRect()
    return {top, left, right, bottom}
  }

  const initElPos = getElPos()

  // Sanity checking/enforcing
  window.scrollTo(0,0)
  document.body.style.overflow = 'hidden'
  el.className = 'card scroll-y overflow-hidden'

  // Create the grabber elements for users to interact with
  const grabberRoot = document.createElement('div')
  grabberRoot.className = 'resizers'
  const grabberEls = [
    document.createElement('div'),  // Center
    document.createElement('div'),  // Top left
    document.createElement('div'),  // Top right
    document.createElement('div'),  // Bottom left
    document.createElement('div')   // Bottom right
  ]
  grabberEls[1].className = 'resizer top-left'
  grabberEls[2].className = 'resizer top-right'
  grabberEls[3].className = 'resizer bottom-left'
  grabberEls[4].className = 'resizer bottom-right'
  grabberEls[0].className = 'resizer center'
  grabberEls[0].style.width = `${initElPos.right - initElPos.left - 20}px`
  grabberEls[0].style.height = `${initElPos.bottom - initElPos.top - 20}px`

  /**
   * Utility function to reset the grabber positions to the bounding box
   * of the main element
   */
  function resetGrabberPos() {
    const initCoords = getElPos()
    
    // Top left
    grabberEls[1].style.top = `${initCoords.top}px`
    grabberEls[1].style.left = `${initCoords.left}px`
    // Top right
    grabberEls[2].style.top = `${initCoords.top}px`
    grabberEls[2].style.left = `${initCoords.right - 10}px`
    // Bottom left
    grabberEls[3].style.top = `${initCoords.bottom - 10}px`
    grabberEls[3].style.left = `${initCoords.left}px`
    // Bottom right
    grabberEls[4].style.top = `${initCoords.bottom - 10}px`
    grabberEls[4].style.left = `${initCoords.right - 10}px`
    // Center
    grabberEls[0].style.top = `${initCoords.top + 10}px`
    grabberEls[0].style.left = `${initCoords.left + 10}px`
    grabberEls[0].style.width = `${initCoords.right - initCoords.left - 20}px`
    grabberEls[0].style.height = `${initCoords.bottom - initCoords.top - 20}px`
  }

  /**
   * Utility function to reset the grabber border to the bounding box of the
   * top left and bottom right grabbers
   */
  function resetGrabberRootPos() {
    const {top, left} = grabberEls[1].getBoundingClientRect()
    const {bottom, right} = grabberEls[4].getBoundingClientRect()
    grabberRoot.style.top = `${top}px`
    grabberRoot.style.left = `${left}px`
    grabberRoot.style.height = `${bottom - top}px`
    grabberRoot.style.width = `${right - left}px`
  }

  /**
   * Utility function to reset both the grabbers and the grabber border
   */
  function resetAll() {
    resetGrabberPos()
    resetGrabberRootPos()
  }

  resetGrabberPos()

  // Append all the elements
  grabberEls.map(el => grabberRoot.appendChild(el))
  el.appendChild(grabberRoot)

  // Top left and bottom right grabber shortcuts
  const resizerTL = grabberEls[1]
  const resizerBR = grabberEls[4]

  /**
   * Allow users to temporarily bring elements up to the surface above
   * other elements so they can resize them again
   */
  grabberRoot.addEventListener('mouseup', () => {
    Array.from(document.querySelectorAll('.card')).map(el => el.style.zIndex = '1')
    grabberRoot.parentElement.style.zIndex = '100'
  })
  window.addEventListener('resize', () => {
    resetGrabberPos()
    resetGrabberRootPos()
  })

  /**
   * Actual resize functionality for each grabber
   */
  for (let i = 0; i < grabberEls.length; i++) {
    const curGrabber = grabberEls[i]
    curGrabber.addEventListener("mousedown", function(e) {
      e.preventDefault()
      window.addEventListener("mousemove", resize)
      window.addEventListener("mouseup", stopResize)
    })

    /**
     * Move the appropriate grabbers so they stay in line with the curGrabber
     * @param {MouseEvent} ev mouse event from the el
     */
    function resize(ev) {
      const top = ev.pageY - 5 + "px"
      const right = ev.pageX - 5 + "px"
      const curGrabberPos = curGrabber.getBoundingClientRect()
      if (!curGrabber.classList.contains("center")) {
        curGrabber.style.top = top
        curGrabber.style.left = right
      } else {
        curGrabber.style.top = `${curGrabberPos.top + ev.movementY}px`
        curGrabber.style.left = `${curGrabberPos.left + ev.movementX}px`
      }

      if (curGrabber.classList.contains("bottom-right")) {
        // Move the bottom left and top right grabbers
        grabberEls[3].style.top = top
        grabberEls[2].style.left = right
      } else if (curGrabber.classList.contains("bottom-left")) {
        // Move the bottom right and top left
        grabberEls[4].style.top = top
        grabberEls[1].style.left = right
      } else if (curGrabber.classList.contains("top-right")) {
        // Move the bottom right and top left grabbers
        grabberEls[1].style.top = top
        grabberEls[4].style.left = right
      } else if (curGrabber.classList.contains("top-left")) {
        // Move the bottom left and top right grabbers
        grabberEls[2].style.top = top
        grabberEls[3].style.left = right
      } else if (curGrabber.classList.contains("center")) {
        // Top left
        grabberEls[1].style.top = `${curGrabberPos.top - 10}px`
        grabberEls[1].style.left = `${curGrabberPos.left - 10}px`
        
        // Top right
        grabberEls[2].style.top = `${curGrabberPos.top - 10}px`
        grabberEls[2].style.left = `${curGrabberPos.right}px`

        // Bottom left
        grabberEls[3].style.top = `${curGrabberPos.bottom}px`
        grabberEls[3].style.left = `${curGrabberPos.left - 10}px`

        // Bottom right
        grabberEls[4].style.top = `${curGrabberPos.bottom}px`
        grabberEls[4].style.left = `${curGrabberPos.right}px`
      }

      // Ensure the grabber root/border stays in line with the grabbers
      resetGrabberRootPos()
    }

    /**
     * Complete the resizing of elements and perform some cleanup
     */
    function stopResize() {
      // Remove unnecesary listeners
      window.removeEventListener("mousemove", resize)
      window.removeEventListener("mouseup", stopResize)
      window.removeEventListener('resize', resetAll)

      /**
       * Distance arrays for the top left and bottom right grabbers
       * Used to figure out which grid template element is closest
       * @type {[number]}
       */
      const BRDists = []
      const TLDists = []

      // Bottom right and top left grabber bounding boxes
      const BRBound = resizerBR.getBoundingClientRect()
      const TLBound = resizerTL.getBoundingClientRect()
      
      // X and Y coordinates of the midpoints for the bottom right and top
      // left grabber elements
      const BRMid = {y: BRBound.bottom - ((BRBound.bottom - BRBound.top) / 2), x: BRBound.right - ((BRBound.right - BRBound.left) / 2)}
      const TLMid = {y: TLBound.bottom - ((TLBound.bottom - TLBound.top) / 2), x: TLBound.right - ((TLBound.right - TLBound.left) / 2)}

      /**
       * Get the diagonal distances for the bottom right and top left to their
       * respective corners
       * 
       * (Example so I remember later on)
       * ┌──────────────────────┬──────────────────────┬──────────────────────┐
       * │←─────╮ Diag Distance ║   to top left of     ║       squares        │
       * │      ╰───────╮       ║                      ║                      │
       * │              ╰────╲─╮║                      ║                      │
       * ├══════════════════════╬══════════════════════╬══════════════════════┤
       * │                      ║█ Top left grabber    ║                      │
       * │                      ║                      ║                      │
       * │                      ║Bottom right grabber █║                      │
       * ├══════════════════════╬══════════════════════╬══════════════════════┤
       * │                      ║                      ║╰─────╮               │
       * │                      ║                      ║      ╰───────╮       │
       * │     Diag Distance    ║  to bottom right of  ║       squares╰──────→│
       * └──────────────────────┴──────────────────────┴──────────────────────┘
       * 
       * The example is supposed to illustrate that the top left grabber will
       * always get the distances to each template elements top left corner
       * using the left and top bounding boxes of the template element
       * 
       * The bottom right will do the opposite and get the bottom right
       * corners of each template element using its bottom and right bounding
       * boxes.
       */
      coords.forEach(coord => {
        // Get bottom right distance to template el
        const distBR = Math.hypot(coord.right - BRMid.x, coord.bottom - BRMid.y)
        BRDists.push(parseInt(distBR))

        // Get top left distance to template el
        const distTL = Math.hypot(coord.left - TLMid.x, coord.top - TLMid.y)
        TLDists.push(parseInt(distTL))
      })

      // Get the array index of the closest corner for both respective grabbers
      const closestBRInd = BRDists.indexOf(Math.min(...BRDists))
      const closestTLInd = TLDists.indexOf(Math.min(...TLDists))

      // Closest element reference
      const closestTLEl = coords[closestTLInd].el
      const closestBREl = coords[closestBRInd].el

      // Get the x and y attributes for setting the rows and columns
      const tlCoords = {
        x: closestTLEl.getAttribute('x'),
        y: closestTLEl.getAttribute('y')
      }
      const brCoords = {
        x: closestBREl.getAttribute('x'),
        y: closestBREl.getAttribute('y')
      }

      // Create a grid area string to span the previously retrieved template elements
      el.style.gridArea = `${tlCoords.y} / ${tlCoords.x} / span ${brCoords.y - tlCoords.y + 1} / span ${brCoords.x - tlCoords.x + 1}`

      // Keep the grid area values to allow easier saving of the attributes at a later point
      el.setAttribute('rs-row', tlCoords.y)
      el.setAttribute('rs-row-span', brCoords.y - tlCoords.y + 1)
      el.setAttribute('rs-col', tlCoords.x)
      el.setAttribute('rs-col-span', brCoords.x - tlCoords.x + 1)

      // reset the grabbers and the grabber root styles
      grabberRoot.style.width = ''
      grabberRoot.style.height = ''
      grabberRoot.style.top = ''
      grabberRoot.style.left = ''
      resetGrabberPos()
      resetGrabberRootPos()
    }
  }


  // Make sure the grabber border goes around the grabbers
  // The grabber border has the added side effect of disabling
  // content below - e.g. links
  resetGrabberRootPos()
}

/**
 * Remove the resizing feature from an element
 * @param {HTMLElement} el Element to remove resizing ability from
 */
export function makeNonResizableDiv(el) {
  document.body.style.overflow = ''
  el.className = 'card scroll-y'
  el.style.zIndex = ''
  /**
   * @type {HTMLElement}
   */
  const resizersRoot = el.querySelector(".resizers")
  if (resizersRoot) resizersRoot.remove()
}

/**
 * Remove the grid template from the dom
 */
export function hideGridTemplate() {
  coords = []
  const gridTemplate = document.querySelector('.grid-container.grid-template')
  if (gridTemplate) gridTemplate.remove()
}

/**
 * Add the grid template to the dom so it shows above everything else
 */
export function showGridTemplate() {
  // Create elements
  const gridTemplateContainer = document.createElement('div')
  const gridTemplate = document.createElement('div')
  
  // Set classes
  gridTemplateContainer.className = 'grid-container grid-template'
  gridTemplate.className = 'grid'

  gridTemplateContainer.appendChild(gridTemplate)
  document.querySelector('main').appendChild(gridTemplateContainer)

  // Generate template elements for a 10 by 6 grid
  for (let y = 1; y <= 10; y++) {
    for (let x = 1; x <= 6; x++) {
      const el = document.createElement("div")
      
      // Set default grid template element attributes
      el.setAttribute("x", x)
      el.setAttribute("y", y)
      el.className = "template"

      gridTemplate.append(el)

      // Push the details of the template element to a variable for easier
      // access to
      const { top, right, bottom, left } = el.getBoundingClientRect()
      coords.push({ top, right, bottom, left, el })
    }
  }
}