<script>
import { months } from '../../utils/defaults.js'
import { txnStatuses } from '../../maps/txnStatuses.js'

export let note

const noteDate = new Date(note.createDate)
const hours = `${noteDate.getHours()}`.padStart(2, '0')
const min = `${noteDate.getMinutes()}`.padStart(2, '0')
if (!note.note && !note.employee) note.note = 'Hardware Change Required' 
</script>

<tr class="note">
  <td>{months[noteDate.getMonth()]}-{noteDate.getDate()} {hours}:{min}</td>
  <td>{note.employee ? note.employee.username : ''}</td>
  <td style="max-width: 310px; word-wrap: break-word;"><pre class="notePre">{note.note}</pre></td>
  <td>{note.transactionStatusId && txnStatuses[note.transactionStatusId] ? txnStatuses[note.transactionStatusId].name : ''}</td>
</tr>