<script>
import { onMount, onDestroy, createEventDispatcher  } from 'svelte'
import { localImages, resolveIMSURL, strToHTML } from '../utils/str.js'
import { removeTicketSearchForm, notifyYellowTicketsLegacy } from '../utils/tickets.js'
import { BDSParser } from '../utils/bds.js'
import { removeScripts } from '../utils/html.js'
import { urlPartFormat } from '../utils/arr.js'
import { formatDate } from '../utils/date.js'
import { addHoverHandlers } from '../utils/tooltips.js'

export let queue

const dispatch = createEventDispatcher()
const titleClick = () => dispatch('titleClick', true)

let interval;
let bodySrc
let lastUpdated = 'init'
let lastUpdateStyle = ''

const removeBuildReqs = () => {
  Array.from(document.querySelectorAll('img[src="img/icons/note.gif"]')).map(el => {
    el.parentElement.remove()
  })
}

// Main updater function - Will fetch the generic queue, strip any funkiness
// then replace the bodySrc outerHTML with the innerHTML of the result
const updater = async () => {
  lastUpdateStyle = 'opacity: 0.5'
  let urlSrc = ''

  if (Array.isArray(queue.url)) urlSrc = urlPartFormat(queue.url)
  else urlSrc = queue.url

  const url = new window.URL(urlSrc)
  const host = url.host

  url.searchParams.append('layout', 'null')

  let res = await fetch(url, {
    credentials: 'include',
    mode: queue.mode
  })
  
  // String based manipulation to clean stuff up
  res = await res.text()
  res = localImages(res)
  if (host === 'internal.softlayer.com') res = resolveIMSURL(res)

  // HTML based manipulation to clean stuff up
  let htmlRes = strToHTML(res)
  if (host === 'internal.softlayer.com') htmlRes = removeTicketSearchForm(htmlRes)
  if (host.indexOf('bds') > -1 && host.indexOf('bds') < 10) htmlRes = BDSParser(htmlRes, host)
  htmlRes = removeScripts(htmlRes)


  if (queue.handleYellow) notifyYellowTicketsLegacy(htmlRes)

  // const el = document.createElement('div')
  bodySrc.innerHTML = htmlRes.innerHTML
  
  if (queue.url[1] === 'srvcTxnByDC') {
    addHoverHandlers(bodySrc)
    removeBuildReqs()
  }
  
  lastUpdated = formatDate()
  lastUpdateStyle = 'opacity: 1'
}

const freq = queue.freq > 0 ? queue.freq*30*1000 : 0

onMount(() => {
  updater()
  if (freq !== 0) setInterval(updater, freq)
})

onDestroy(() => {
  clearInterval(updater)
})
</script>

<div class="card-header">
  <span class="h6" on:click={titleClick}>{queue.title}</span>
  <span class="h6" style={lastUpdateStyle} on:click={updater}>{lastUpdated}</span>
</div>
<div class="card-body" bind:this={bodySrc}></div>
