<script>
  import TXN from './TXNSub/TXN.svelte'

  import { onMount, onDestroy, tick, createEventDispatcher } from 'svelte'
  import { SLAPIRequest, getCookie } from '../utils/api.js'
  import { dcs } from '../maps/dcs.js'
  import { formatDate } from '../utils/date.js'

  export let queue

  const dispatch = createEventDispatcher()
  const titleClick = () => dispatch('titleClick', true)

  let partArr = queue.url
  let url = ''
  let lastUpdated = 'init'
  let lastUpdateStyle = ''
  let txns = []

  if (partArr[1] === 'srvcTxnByDC' && partArr[2] && partArr[2].startsWith('dc_')) url = `api:txn:${dcs[partArr[2]]}`

  // Main updater function. Will run an API call using your current IMS creds and
  // will replace the current {txns} array with the results
  const updater = async () => {
    lastUpdateStyle = 'opacity: 0.5'
    
    const apiReq = new SLAPIRequest({
      service: 'SoftLayer_Provisioning_Version1_Transaction',
      func: `getCurrentTransactions/${url.substr(8)}`,
      username: await getCookie('ims_int_portal_login'),
      password: await getCookie('ims_int_portal_hash')
    })

    apiReq.limit(1).offset(0).mask.set({
      id: {},
      currentTransactionErrorCount: {
        // createDate: {},
        // scriptName: {},
        // transactionStatusId: {}
      },
      deadlineDate: {},
      secondsOverdue: {},
      locationId: {},
      hardware: {
        id: {},
        serialNumber: {},
        hostname: {},
        locationPathString: {}
      },
      guest: {
        id: {},
        hostname: {},
        locationPathString: {}
      },
      notes: {
        id: {},
        createDate: {},
        note: {},
        employee: {
          id: {},
          username: {}
        },
        transactionStatusId: {}
      },
      transactionStatusId: {},
      transactionStatus: {
        id: {},
        keyName: {},
        name: {},
        executionPath: {}
      },
      processInstance: {
        id: {},
      },
      transactionGroupId: {},
      transactionGroup: {
        id: {},
        keyName: {}
      },
      remainingAttempts: {},
      statusChangeDate: {},
      mode: {
        id: {},
        keyName: {}
      },
      job: {},
      modifyDate: {}
    })

    const tempTxns = await apiReq.exec()
    
    txns = []
    await tick()
    txns = tempTxns
    
    lastUpdated = formatDate()
    lastUpdateStyle = 'opacity: 1'
  }

  const freq = queue.freq > 0 ? queue.freq * 30 * 1000 : 0

  onMount(() => {
    updater()
    if (freq !== 0) setInterval(updater, freq)
  })

  onDestroy(() => {
    clearInterval(updater)
  })
</script>

<div class="card-header">
  <span class="h6" on:click={titleClick}>{queue.title}</span>
  <span class="h6" style={lastUpdateStyle} on:click={updater}>{lastUpdated}</span>
</div>
<div class="card-body">
  <table class="helperTable" cellspacing="0" cellpadding="0">
    <thead>
      <tr>
        <th class="txnColumn150">HWTXN</th>
        <th class="txnColumn150">Host</th>
        <th class="txnColumn150">Location</th>
        <th class="txnColumn150">Group (Path)</th>
        <th class="txnColumn150">Status</th>
        <th></th>
        <th class="txnColumn70">Overdue</th>
        <th class="txnColumn150">Deadline</th>
      </tr>
    </thead>
    <tbody>
    {#each txns as txn}
      <TXN txn={txn}></TXN>
    {/each}
    </tbody>
  </table>
</div>