import { parts } from '../maps/urlParts.js'
import { dcs } from '../maps/dcs.js'
import { deps } from '../maps/departments.js'
import { ticketStatus } from '../maps/ticketStatuses.js'
import { escGroup } from '../maps/escalationGroups.js'

/**
 * Convert a url array into a string
 * @param {string[]} partArr URL part array
 * @returns {string}
 */
export const urlPartFormat = (partArr = []) => {
  return partArr.map(part => {
    let ref = parts
    let str = ''

    if (!part) return ''

    if (part.startsWith('dep_')) {
      str += parts.ticketQPartial
      ref = deps
    }
    if (part.startsWith('ticketStatus_')) {
      str += parts.ticketStatusPartial
      ref = ticketStatus
    }
    if (part.startsWith('dc_')) {
      if (partArr[1] !== 'srvcTxnByDC') str += ref.ticketDCPartial
      ref = dcs
    }
    if (part.startsWith('escGroup_')) {
      // str += ref.escPartial
      ref = escGroup
    }

    if (!ref[part]) throw new ReferenceError(`Invalid selection/s in ${partArr}`)

    return str + ref[part]
  }).join('')
}

/**
 * Make a new unique array from a provided input
 * @param {any[]} arr Array to make unique
 */
export const unique = (arr = []) => [...new Set(arr)]
