<script>
  import Generic from './Generic.svelte'
  import BDS from './BDS.svelte'
  import TXNAPI from './TXNAPI.svelte'
  import TicketAPI from './TicketAPI.svelte'

  import {onMount} from 'svelte'

  export let queue
  
  const url = queue.url
  const styles = `grid-column:${queue.colStart}/span ${queue.colSpan};grid-row:${queue.rowStart}/span ${queue.rowSpan}`
  let init = true
  let el
  let fullscreen = false

  $: classList = `card scroll-y ${fullscreen ? 'lsfs' : ''}`

  onMount(() => {
    if (init) {
      init = false
      const config = {
        target: el,
        props: {
          queue
        }
      }

      if (queue.api) {
        if (queue.url[1] === 'srvcTxnByDC') {
          queue.app = new TXNAPI(config)
        }

        if (queue.url[1] === 'srvcTicket' || queue.url[1] === 'srvcAdvTicketSearch') {
          queue.app = new TicketAPI(config)
        }
      } else if (queue.url.includes('://bds')) { // Create a BDS card
        queue.app = new BDS(config)
      } else { // Create a generic queue card
        queue.app = new Generic(config)
      }

      queue.el = el

      queue.app.$on('titleClick', () => {fullscreen = !fullscreen})
    }
  })
</script>

<div class={classList} style={styles} bind:this={el}></div>