<script>
import { months } from '../../utils/defaults.js'
import { txnStatuses } from '../../maps/txnStatuses.js'

export let error

const errorDate = new Date(error.createDate)
const hours = `${errorDate.getHours()}`.padStart(2, '0')
const min = `${errorDate.getMinutes()}`.padStart(2, '0')
</script>

<tr class="error">
  <td>{months[errorDate.getMonth()]}-{errorDate.getDate()} {hours}:{min}</td>
  <td style="max-width: 310px; word-wrap: break-word;">{error.scriptName} | {error.message}</td>
  <td>{error.transactionStatusId && txnStatuses[error.transactionStatusId] ? txnStatuses[error.transactionStatusId].name : ''}</td>
</tr>