export const defaultHeader = '<div><h1 class="rounded">'
export const invalidURLStr = `${defaultHeader}Invalid URL</h1>`

export const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']

export const color = {
  light_blue: '#99CCFF',  dt_light_blue: '#003d7a',
  dark_blue: '#003D7A',   dt_dark_blue: '#001d5a',
  light_green: '#00FF66', dt_light_green: '#004c1e',
  dark_green: '#33CC00',  dt_dark_green: '#0f3d00',
  purple: '#A0A0E0',      dt_purple: '#4b4b80',
  red: '#FF0000',         dt_red: '#4c0000',
  yellow: '#FFFF00',      dt_yellow: '#4c4c00',
  coral: '#FF6633',       dt_coral: '#81351c',
}


export const handleSignedOutIMS = async data => {
  let text = await data.text()
  if (text.indexOf('<font class="med16_red">login</font>') > -1) text = `<div>${defaultHeader}Please log in to IMS in another tab</h1><h4 class="rounded">You can then click on the "Last Updated" text above on each of the required panels to manually refresh the view</h4></div>`

  return text
}
