import { differenceInMinutes, differenceInSeconds, differenceInDays, differenceInHours } from 'date-fns'

/**
 * Format a new date object to be human-readable
 * @param {Date} dateObj Date to format
 */
export function formatDate (dateObj = new Date()) {
  const year = dateObj.getFullYear()
  const month = `${dateObj.getMonth() + 1}`.padStart('2', '0')
  const day = `${dateObj.getDate()}`.padStart('2', '0')
  const hour = `${dateObj.getHours()}`.padStart('2', '0')
  const minute = `${dateObj.getMinutes()}`.padStart('2', '0')
  const second = `${dateObj.getSeconds()}`.padStart('2', '0')

  return `${year}/${month}/${day} ${hour}:${minute}:${second}`
}

/**
 * Convert a number of seconds to ...hh:mm:ss format
 * @param {number} sec Number of seconds to convert
 * @returns {string}
 */
export function secToHMS (sec) {
  if (sec < 0) return '00:00:00'

  const h = Math.floor(sec / 3600)
  const m = Math.floor(sec % 3600 / 60)
  const s = Math.floor(sec % 3600 % 60)

  const hStr = h > 0 ? `${h}`.padStart(2, '0') + ':' : '00:'
  const mStr = `${m}`.padStart(2, '0') + ':'
  const sStr = `${s}`.padStart(2, '0')
  return hStr + mStr + sStr
}

/**
 * Get the difference between two dates in seconds, minutes, hours, or days
 * @param {string} time Time in UTC compatible format
 */
export const diff = time => {
  const now = new Date()
  const modDate = new Date(time)

  const diffSec = differenceInSeconds(now, modDate)
  const diffMin = differenceInMinutes(now, modDate)
  const diffHour = differenceInHours(now, modDate)
  const diffDay = differenceInDays(now, modDate)

  return diffSec <= 90 ? `${diffSec}s` : diffMin <= 60 ? `${diffMin}m` : diffHour <= 24 ? `${diffHour}h` : `${diffDay}d`
}
