<script>
  import { ticketStatuses, ticketGroups } from '../../maps/tickets'
  import { diff } from '../../utils/date.js'

  export let ticket

  const statusMap = ticketStatuses.map(stat => stat.id)
  const groupMap = ticketGroups.map(group => group.id)

  const id = ticket.id
  const title = ticket.title
  const location = (ticket.location && ticket.location.name) ? ticket.location.name : ''
  const account = (ticket.account) ? ticket.account : ''
  const status = (ticketStatuses[statusMap.indexOf(ticket.statusId)]) ? ticketStatuses[statusMap.indexOf(ticket.statusId)].name : ''
  const escalation = ticket.escalation ? `<a href="https://internal.softlayer.com/Escalation/manage/${ticket.escalation.id}">${ticket.escalation.statusKeyName}</a>` : ''
  const assignedUser = ticket.assignedUser
  const { modifyDate, createDate, customerWaitTime } = ticket
  const employee = ticket.assignedEmployee ? `<a href="https://internal.softlayer.com/Employee/employeeView/${ticket.assignedEmployee.id || ''}">${ticket.assignedEmployee.username}</a>` : ''
  const serverCount = ticket.attachedHardwareCount
  let servers = 'None'
  if (serverCount === 1) {
    servers = `<a href="https://internal.softlayer.com/Hardware/view/${(ticket.attachedHardware) ? ticket.attachedHardware.id : ''}">${(ticket.attachedHardware) ? ticket.attachedHardware.hostname : 'No hostname'}</a>`
  } else if (serverCount > 1) {
    servers = `${serverCount} servers`
  }
  const group = ticketGroups[groupMap.indexOf(ticket.groupId)].name
  let yellow = ''
  ticket.state.map(state => {
    if (state.stateTypeId === 1) {
      yellow = diff(state.modifyDate)
    }
  })
  const supportClassification = ticket.supportClassification || ''
  const autoclose = ticket.autocloseFlag ? 'Yes' : ''
</script>

<li class="ticket {yellow !== '' ? 'alertTicket' : ''}">
  <span class="location">{location}</span>
  <span class="title">(<a href="https://internal.softlayer.com/Ticket/ticketEdit/{id}">{id}</a>) <a href="https://internal.softlayer.com/Ticket/ticketPreview/{id}">{title}</a></span>
  <span class="account">Account: <a href="https://internal.softlayer.com/Customer/viewCustomer/{account ? account.id : ''}">{account ? account.companyName : ''}</a></span>
  <span class="status">Status: {status}</span>
  <span class="escalation">Escalation: {@html escalation}</span>
  <span class="owner">Owner: {assignedUser ? assignedUser.username : ''}</span>
  <span class="modified">Modified: {modifyDate}</span>
  <span class="created">Created: {createDate}</span>
  <span class="staff">Staff: {@html employee}</span>
  <span class="server">Server: {@html servers}</span>
  <span class="group">Group: {group}</span>
  <span class="wait">Customer Wait: {customerWaitTime ? diff(customerWaitTime) : ''}</span>
  <span class="yellow">Yellow: {yellow}</span>
  <span class="autoclose">AC: {autoclose}</span>
  <span class="tier">Support Tier: {supportClassification}</span>
</li>
