<script>
  import { onMount, onDestroy } from 'svelte'
  import { secToHMS } from '../../utils/date.js'
  
  import { color } from '../../utils/defaults.js'
  import { addTimeHover, removeTimeHover } from '../../utils/tooltips.js'
  import { format } from 'date-fns'

  export let txn

  let deadlineEl, deadlineTooltipEl
  
  const storage = window.localStorage
  const opts = JSON.parse(storage.getItem('opts') || '{}')
  const dark = opts.dark || false
  function darkify(colorName = '') {
    if (dark) return color[`dt_${colorName}`]
    else return color[colorName]
  }

  // Deadline of TXN in Date() compatible format
  const deadline = new Date(txn.deadlineDate)
  // Readable string version of deadline for users
  let deadlineDate = deadline.toTimeString()

  const startGMT = deadlineDate.indexOf('GMT')
  const startCountry = deadlineDate.indexOf('(')
  const timezone = deadlineDate.substr(startGMT, startCountry - startGMT - 1)
  deadlineDate = `${deadlineDate.substr(0, 5)} (${timezone})`
  // Last time the txn was changed in Date() compatible format
  const statusChangeDate = new Date(txn.statusChangeDate)
  // Last time the txn was modified in Date() compatible format
  const modifyDate = new Date(txn.modifyDate)

  // Some vars for the `default` route
  const curDate = new Date().getTime()
  const threeMinAgo = new Date(curDate - 60000 * 3)

  // Default deadline colour is light green
  let deadlineColor = darkify('light_green')
  // Same foes for overdue colour. Asume everything's fine
  let overdueColor = darkify('light_green')


  if (deadline < new Date()) {
    deadlineColor = darkify('red')
  }

  switch (txn.transactionStatus.name) {
    case 'COMPLETE': // COMPLETE
      overdueColor = darkify('light_green')
      if (statusChangeDate > deadline) deadlineColor = darkify('red')
      else deadlineColor = darkify('dark_green')
      break
    case 'HARDWARE_ON_ORDER': // HARDWARE_ON_ORDER
      overdueColor = darkify('light_blue')
      deadlineColor = darkify('light_blue')
      break
    case 'RECLAIM_WAIT': // RECLAIM_WAIT
    case 'TICKET_SCHEDULED_ACTION_WAIT': // TICKET_SCHEDULED_ACTION_WAIT
      overdueColor = darkify('dark_blue')
      deadlineColor = darkify('dark_blue')
      break
    case 'PE_ERASEDRIVES': // PE_ERASEDRIVES
      overdueColor = darkify('light_green')
      deadlineColor = darkify('light_green')
      if (deadline < new Date()) {
        overdueColor = darkify('red')
        deadlineColor = darkify('red')
      }
      break
    case 'HOLD': // HOLD
      overdueColor = darkify('yellow')
      break
    case 'ASSIGN_SERVER_MANUALLY': // ASSIGN_SERVER_MANUALLY
      overdueColor = txn.secondsOverdue > 1 ? darkify('red') : darkify('light_blue')
      break
    case 'HARDWARE_CHANGE_PENDING': // HARDWARE_CHANGE_PENDING
      overdueColor = txn.secondsOverdue > 1 ? darkify('coral') : darkify('light_blue')
      break
    case 'PE_ERASE_TIMEOUT': // PE_ERASE_TIMEOUT
    case 'MANUAL_DRIVE_CONFIGURATION': // MANUAL_DRIVE_CONFIGURATION
      overdueColor = darkify('red')
      break
    case 'CHECK_CLOUD_DISK_TEMPLATE_TRANSFER': // CHECK_CLOUD_DISK_TEMPLATE_TRANSFER
      overdueColor = darkify('light_green')
      if (txn.remainingAttempts < 1 && txn.transactionStatus.executionPath) overdueColor = darkify('red')
      break
    default:
      if (txn.transactionStatus.name.includes('failure')) overdueColor = darkify('red')
      else if (txn.secondsOverdue > 0 && modifyDate < threeMinAgo) overdueColor = darkify('red')
      else if (txn.remainingAttempts < 1 && txn.transactionStatus.executionPath) overdueColor = darkify('red')
      else overdueColor = darkify('light_green')
      break
  }

  if (txn.mode.keyName === 'TEST') {
    overdueColor = darkify('purple')
    deadlineColor = darkify('purple')
  }

  overdueColor = `background-color:${overdueColor} ;border-right:solid #222 1px`
  deadlineColor = `background-color:${deadlineColor}`

  const now = new Date().getTime()
  const txnDeadlineDate = deadline.getTime()
  const deadlineOverdueSeconds = Math.round((now - txnDeadlineDate) / 1000)
  const deadlineOverdueString = () => {
    if (deadlineOverdueSeconds < 0) return `Overdue in: ${secToHMS(deadlineOverdueSeconds * -1)}`
    else if (deadlineOverdueSeconds > 0) return `Overdue by: ${secToHMS(deadlineOverdueSeconds)}`
  }

  onMount(() => {
    addTimeHover(deadlineEl)
  })

  onDestroy(() => {
    removeTimeHover(deadlineEl)
  })
</script>

<td style="text-align: center;{overdueColor}">
  <span style="pointer-events: none;">{secToHMS(txn.secondsOverdue)}</span>
</td>
<td style="text-align: center;{deadlineColor}" bind:this={deadlineEl}>
  <span style="pointer-events: none;">{format(deadline, 'HH:mm:ss - yyyy-LLL-dd')}</span>
  <div bind:this={deadlineTooltipEl} class="timeEl" style="display: none; right: 0px; top: 0px;">
    <p>{timezone}</p>
    <hr>
    <p>{deadlineOverdueString()}</p>
  </div>
</td>