<script>
  import Ticket from './TicketSub/Ticket.svelte'

  import { onMount, onDestroy, tick, createEventDispatcher } from 'svelte'
  import { SLAPIRequest, getCookie } from '../utils/api.js'
  import { formatDate } from '../utils/date.js'
  import { unique } from '../utils/arr.js'
  import {
    ticketGroupFunc,
    ticketLocationFunc,
    ticketStateFunc,
    ticketStatusFunc,
    ticketAutoclose,
    notifyYellowTickets
  } from '../utils/tickets.js'
  import { ticketStatus } from '../maps/ticketStatuses.js'

  import { deps } from '../maps/departments.js'
  import { dcs } from '../maps/dcs.js'
  
  export let queue
  
  const dispatch = createEventDispatcher()
  const titleClick = () => dispatch('titleClick', true)

  let partArr = queue.url.flat()
  let lastUpdated = 'init'
  let lastUpdateStyle = ''

  const filter = {
    groups: [],
    statuses: [],
    states: [],
    locations: [],
    autoclose: undefined
  }

  partArr.map(part => {
    if (part) {
      if (part.startsWith('autoclose_')) {
        filter.autoclose = part === 'autoclose_true' ? true : part === 'autoclose_false' ? false : undefined
      }
      if (part.startsWith('group_')) {
        filter.groups.push(part.substr(6))
      }
      if (part.startsWith('status_')) {
        if (part === 'status_yellow') {
          filter.states.push(1, '!= 3')
          filter.statuses.push(1004, 1001)
        } else {
          filter.statuses.push(part.substr(7))
        }
      }
      if (part.startsWith('location_')) {
        filter.locations.push(part.substr(9))
      }
      if (part.startsWith('state_')) {
        filter.states.push(part.substr(6))
      }
      if (part.startsWith('stateNot_')) {
        filter.states.push(`!= ${part.substr(9)}`)
      }

      // Legacy
      if (part.startsWith('dep_')) {
        filter.groups.push(deps[part])
      }
      if (part.startsWith('ticketStatus_')) {
        if (part === 'ticketStatus_Assigned/Open') {
          filter.statuses.push(1004, 1001)
        } else if (part === 'ticketStatus_Yellow') {
          filter.states.push(1, '!= 3')
          filter.statuses.push(1004, 1001)
        } else if (part === 'ticketStatus_Orphan') {
          filter.states.push(6)
          filter.statuses.push(1004, 1001)
        } else {
          filter.statuses.push(ticketStatus[part])
        }
      }
      if (part.startsWith('dc_')) {
        filter.locations.push(dcs[part])
      }
    }
  })

  let tickets = []

  const conf = {
    limit: 100,
    offset: 0
  }
  let totalTickets = 0
  let showNext = false
  let showPrev = false

  // Main updater function. Will run an API call using your current IMS creds and
  // will replace the current {txns} array with the results
  const updater = async () => {
    lastUpdateStyle = 'opacity: 0.5'
    
    const apiReq = new SLAPIRequest({
      service: 'SoftLayer_Ticket',
      func: 'getAllObjects',
      username: await getCookie('ims_int_portal_login'),
      password: await getCookie('ims_int_portal_hash')
    })

    apiReq.filter({
      ...ticketGroupFunc(...unique(filter.groups)),
      ...ticketLocationFunc(...unique(filter.locations)),
      ...ticketStateFunc(...unique(filter.states)),
      ...ticketStatusFunc(...unique(filter.statuses)),
      ...ticketAutoclose(filter.autoclose)
    })

    apiReq.limit(conf.limit).offset(conf.offset).mask.set({
      id: {},
      autocloseFlag: {},
      createDate: {},
      customerWaitTime: {},
      groupId: {},
      modifyDate: {},
      statusId: {},
      supportClassification: {},
      title: {},
      attachedHardwareCount: {},
      account: {
        id: {},
        companyName: {}
      },
      assignedEmployee: {
        id: {},
        username: {}
      },
      assignedUser: {
        id: {},
        username: {}
      },
      attachedHardware: {
        id: {},
        hostname: {}
      },
      escalation: {
        id: {},
        statusKeyName: {}
      },
      location: {
        id: {},
        name: {}
      },
      state: {
        id: {},
        stateTypeId: {},
        createDate: {},
        modifyDate: {}
      }
    })

    const tempTickets = await apiReq.exec()
    
    // Update things
    totalTickets = apiReq.totalItems
    tickets = []
    if (conf.offset + conf.limit < totalTickets) showNext = true
    else showNext = false
    
    if (conf.offset !== 0 && conf.offset - conf.limit > -1) showPrev = true
    else showPrev = false
    await tick()
    tickets = tempTickets
    if (queue.handleYellow) notifyYellowTickets(totalTickets)
    
    lastUpdated = formatDate()
    lastUpdateStyle = 'opacity: 1'
  }

  const freq = queue.freq > 0 ? queue.freq * 30 * 1000 : 0

  onMount(() => {
    updater()
    if (freq !== 0) setInterval(updater, freq)
  })

  onDestroy(() => {
    clearInterval(updater)
  })
</script>

<div class="card-header">
  <span class="h6" on:click={titleClick}>{queue.title}</span>
  <span class="h6" style={lastUpdateStyle} on:click={updater}>{lastUpdated}</span>
  {#if showPrev}
    <span on:click={() => {conf.offset = conf.offset - conf.limit; showPrev=false; updater()}}>&lt;</span>
  {/if}
  <span>{tickets.length > 0 ? conf.offset + 1 : 0} - {conf.offset + tickets.length}/{totalTickets}</span>
  {#if showNext}
    <span on:click={() => {conf.offset = conf.offset + conf.limit; showNext=false; updater()}}>&gt;</span>
  {/if}
</div>
<div class="card-body">
  <ul class="ticketList">
    {#each tickets as ticket}
      <Ticket ticket={ticket}></Ticket>
    {/each}
  </ul>
</div>
{#if tickets.length > 2}
  <div class="card-body">
    {#if showPrev}
      <span on:click={() => {conf.offset = conf.offset - conf.limit; showPrev=false; updater()}}>&lt;</span>
    {/if}
    <span>{tickets.length > 0 ? conf.offset + 1 : 0} - {conf.offset + tickets.length}/{totalTickets}</span>
    {#if showNext}
      <span on:click={() => {conf.offset = conf.offset + conf.limit; showNext=false; updater()}}>&gt;</span>
    {/if}
  </div>
{/if}