const storage = window.localStorage
const opts = JSON.parse(storage.getItem('opts') || '{}')

/**
 * Add a note hover handler for a txn (legacy/ims)
 * @param {HTMLElement} icon Icon to add hover handler for
 */
export function addHoverHandlers (doc = document) {
  const els = Array.from(doc.querySelectorAll('div[id^="last_note_div_"]'))
  els.map(el => {
    const icon = el.previousElementSibling

    icon.onmouseover = null
    icon.onmouseout = null
    icon.onclick = null

    icon.addEventListener('mouseover', mousoverNoteListener)
    icon.addEventListener('mouseout', mousoutNoteListener)

    if (opts.prettyNotes) {
      const notesCont = icon.nextElementSibling
      Array.from(notesCont.querySelectorAll('table tbody tr')).map(note => {
        const content = note.querySelector('td[style="max-width: 310px; word-wrap: break-word;"]')
        if (content) {
          const pre = document.createElement('pre')
          pre.style.maxWidth = '100%'
          pre.style.wordWrap = 'break-word'
          pre.style.whiteSpace = 'pre-wrap'
          pre.style.margin = '2px'
          pre.innerText = content.innerText.trim().split()
          content.innerHTML = pre.outerHTML
        }
      })
    }
  })
}
export function addNoteHover (icon) {
  icon.onmouseover = null
  icon.onmouseout = null
  icon.onclick = null

  icon.addEventListener('mouseover', mousoverNoteListener)
  icon.addEventListener('mouseout', mousoutNoteListener)
}
export const addTimeHover = (field) => {
  field.addEventListener('mouseover', mousoverTimeListener)
  field.addEventListener('mouseout', mousoutTimeListener)
}
export const addIDHover = (field) => {
  field.addEventListener('mouseover', mousoverIDListener)
  field.addEventListener('mouseout', mousoutIDListener)
}

/**
 * Remove a note hover handler for a txn
 * @param {HTMLElement} icon Icon to add hover handler for
 */
export function removeNoteHover (icon) {
  icon.removeEventListener('mouseover', mousoverNoteListener)
  icon.removeEventListener('mouseout', mousoutNoteListener)
}
export function removeTimeHover (field) {
  field.removeEventListener('mouseover', mousoverTimeListener)
  field.removeEventListener('mouseout', mousoutTimeListener)
}
export function removeIDHover (field) {
  field.removeEventListener('mouseover', mousoverIDListener)
  field.removeEventListener('mouseout', mousoutIDListener)
}

const mousoverNoteListener = e => ToggleNote(e.target.parentElement.nextElementSibling, e.target.parentElement)
const mousoutNoteListener = e => ToggleNote(e.target.parentElement.nextElementSibling)
const mousoverTimeListener = e => ToggleTime(e.target.lastElementChild, e.target)
const mousoutTimeListener = e => ToggleTime(e.target.lastElementChild)
const mousoverIDListener = e => ToggleID(e.target.nextElementSibling, e.target)
const mousoutIDListener = e => ToggleID(e.target.nextElementSibling)

function ToggleNote (notesDiv, icon) {
  if (notesDiv && notesDiv.style.display === 'none') {
    const docHeight = window.height
    notesDiv.style.display = 'block'
    if (icon) {
      const boundary = notesDiv.getBoundingClientRect()
      const offset = {
        top: boundary.top,
        left: boundary.left
      }

      const height = notesDiv.offsetHeight
      const pos = {
        left: notesDiv.offsetLeft,
        top: notesDiv.offsetTop
      }

      notesDiv.style.left = `${icon.offsetLeft + 20}px`
      notesDiv.style.top = `${(offset.top + height > docHeight) ? pos.top - (offset.top + height - docHeight) : pos.top}px`
    }
  } else if (notesDiv) {
    notesDiv.style.display = 'none'
  }

  return false
}

function ToggleTime (timeDiv, field) {
  if (timeDiv && timeDiv.style.display === 'none') {
    timeDiv.style.display = 'block'

    if (field) {
      const docWidth = window.innerWidth
      const fieldBoundaries = field.getBoundingClientRect()

      timeDiv.style.right = `${docWidth - fieldBoundaries.left}px`
      timeDiv.style.top = `${fieldBoundaries.top}px`
    }
  } else if (timeDiv) {
    timeDiv.style.display = 'none'
  }

  return false
}

function ToggleID (errorsDiv, IDField) {
  if (errorsDiv && errorsDiv.style.display === 'none') {
    errorsDiv.style.display = 'block'

    if (IDField) {
      const fieldBoundaries = IDField.getBoundingClientRect()

      errorsDiv.style.left = `${fieldBoundaries.right - fieldBoundaries.left + 20}px`
      // errorsDiv.style.top = `${fieldBoundaries.top}px`
    }
  } else if (errorsDiv) {
    errorsDiv.style.display = 'none'
  }

  return false
}
