export const notifyYellowTickets = (numTickets) => {
  if (numTickets !== 0 && numTickets !== '0') {
    if (!('Notification' in window)) toast('This browser does not support desktop notification', 4000, 'error')

    // Let's check whether notification permissions have already been granted
    else {
      let notification
      const notificationOpts = {
        body: `There ${numTickets === 1 ? 'is' : 'are'} currently ${numTickets} yellowed ticket${numTickets === 1 ? '' : 's'}`,
        icon: 'img/dts/dts128.png'
      }

      if (window.Notification.permission === 'granted') {
        notification = new window.Notification('Dash', notificationOpts)
      } else if (window.Notification.permission !== 'denied') { // Otherwise, we need to ask the user for permission
        window.Notification.requestPermission()
          .then(permission => {
            if (permission === 'granted') {
              notification = new window.Notification('Dash', notificationOpts)
            }
          })
      }

      setTimeout(notification.close.bind(notification), 4000)
    }
  }
}

/**
 * create a web notification if there's a new yellow ticket
 * @param {HTMLElement} html HTML element of the ticket queue
 */
export const notifyYellowTicketsLegacy = (html) => {
  notifyYellowTickets(html.querySelectorAll('tr.alertTicket.bottom-border').length)
}
