/**
 * Fix IMS' image scheme for local use
 * @param {String} str HTML string to replace ims images with local ones
 */
export const localImages = (str = '') => str.replace(/\/img\//gi, 'img/')

/**
 * Fix all URLs to be pointing to https://internal.softlayer.com/*
 * @param {String} str HTML string to fix IMS URLS
 */
export const resolveIMSURL = (str = '') => str.replace(/href="\//gi, 'target="_blank" href="https://internal.softlayer.com/')

/**
 * Convert a string to a proper HTML fragment
 * @param {string} str HTML string to convert to an HTML fragment
 * @returns {HTMLElement}
 */
export function strToHTML (str = '') {
  const el = document.createElement('div')
  const range = document.createRange()
  // or whatever context the fragment is to be evaluated in.
  const parseContext = document.body
  range.selectNodeContents(parseContext)
  const fragment = range.createContextualFragment(str)

  el.appendChild(fragment)

  return el
}
