import { strToHTML } from './str'
import { defaultHeader } from './defaults'

const axdScriptRegMain = /(<script src="\/WebResource.axd\?[\w\d\s=&;"\/>-]+<\/script>)/gim
const axdScriptRegSub = /("\/ScriptResource.axd\?[\w\d\s=&;\/>-]+")/gim
const axdResReg = /("\/WebResource.axd\?[\w\d\s=&;\/>-]+")/gim

const replaceBDSResources = inp => inp.replace(axdScriptRegMain, '').replace(axdScriptRegSub, '""').replace(axdResReg, '""')

/**
 * Remove the jankiness of BDS
 * @param {HTMLElement} html HTML string to modify
 * @param {string} bdsURL BDS base url
 */
export const BDSParser = (html, bdsURL) => {
  let el = html.querySelector('#DefaultContent_gvOrders')

  if (el) {
    el = el.parentElement
    const extensionLinks = [...el.querySelectorAll('a:not([href^="http"]):not([href^="javascript"])')]
    const jsLinks = [...el.querySelectorAll('a[href^="javascript"]')]

    // Fix up any relative links
    extensionLinks.map(el => {
      const ind = el.href.indexOf('/pages/tab/')
      if (el.href.substr(ind + 11) !== '') {
        el.href = `${bdsURL.startsWith('http') ? '' : 'https://'}${bdsURL}/${el.href.substr(ind + 11)}`
        el.target = '_blank'
      }
    })

    // Change any javascript hrefed links to just spans with the same content
    jsLinks.map(el => {
      const newNode = document.createElement('span')
      newNode.innerText = el.innerText
      el.replaceWith(newNode)
    })
  } else el = strToHTML(`${defaultHeader}Please log in to the selected BDS (${bdsURL}) in another tab</h1><h4 class="rounded">You can then click on the "Last Updated" text above on each of the required panels to manually refresh the view</h4></div>`)

  return el
}

export const getInitialBDSPage = async url => {
  const tempURL = new window.URL(url)
  const bdsURL = new URL(`${tempURL.protocol}//${tempURL.host}/Default.aspx`);

  // Retrieve initial page
  let initial = await fetch(bdsURL, {
    credentials: 'include',
  })

  // Convert the response to a string, then into an HTML fragment
  initial = await initial.text()
  initial = replaceBDSResources(initial)
  
  let htmlInit = strToHTML(initial)

  // Seperate rows from table body for later
  const tableBody = htmlInit.querySelector('#DefaultContent_gvOrders>tbody')

  // Make sure the user is logged in
  if (tableBody) {
    // Get how many bds jobs there are for pagination
    const jobCount = Number.parseInt(htmlInit.querySelector('#DefaultContent_lblTotalRecords').innerText) || 0
    
    let rows = Array.from(tableBody.children)
  
    if (jobCount > 50) {
      const opts = {
        __VIEWSTATE: htmlInit.querySelector('#__VIEWSTATE').value,
        __VIEWSTATEGENERATOR: htmlInit.querySelector('#__VIEWSTATEGENERATOR').value,
        __EVENTVALIDATION: htmlInit.querySelector('#__EVENTVALIDATION').value,
      }

      for (let page = 2; page <= Math.ceil(jobCount/50); page++) {
        const pageRes = await getAdditionalPages(page, opts, `${tempURL.protocol}//${tempURL.host}`)

        rows = rows.concat(...pageRes.rows)

        opts.__VIEWSTATE = pageRes.__VIEWSTATE
        opts.__VIEWSTATEGENERATOR = pageRes.__VIEWSTATEGENERATOR
        opts.__EVENTVALIDATION = pageRes.__EVENTVALIDATION
      }
    }

    // Replicate BDS format
    const tableEl = document.createElement('table')
    tableEl.cellSpacing = 0
    tableEl.cellPadding = 4
    tableEl.style.width = '100%'
    tableEl.style.borderCollapse = 'collapse'
    const tableBodyEl = document.createElement('tbody')

    rows.map(row => tableBodyEl.appendChild(row))

    tableEl.appendChild(tableBodyEl)

    const extensionLinks = [...tableEl.querySelectorAll('a:not([href^="http"]):not([href^="javascript"])')]
    const jsLinks = [...tableEl.querySelectorAll('a[href^="javascript"]')]

    // Fix up any relative links
    extensionLinks.map(el => {
      const ind = el.href.indexOf('/pages/tab/')
      if (el.href.substr(ind + 11) !== '') {
        el.href = `${tempURL.protocol}//${tempURL.host}/${el.href.substr(ind + 11)}`
        el.target = '_blank'
      }
    })

    // Change any javascript hrefed links to just spans with the same content
    jsLinks.map(el => {
      const newNode = document.createElement('span')
      newNode.innerText = el.innerText
      el.replaceWith(newNode)
    })

    return tableEl
  } else return strToHTML(`${defaultHeader}Please log in to the selected BDS (${tempURL.protocol}//${tempURL.host}/) in another tab</h1><h4 class="rounded">You can then click on the "Last Updated" text above on each of the required panels to manually refresh the view</h4></div>`)
}

const getAdditionalPages = async (page, opts, bdsURL) => {
  const body = {
    '--url': bdsURL,
    '--body': JSON.stringify({
      ...opts,
      __EVENTTARGET: `ctl00$DefaultContent$Pagination2$Page${page}`
    })
  }

  let res = await new Promise(resolve => chrome.runtime.sendMessage({ getBDSPage: true, opts: body }, resolve))
  res = replaceBDSResources(res)

  const htmlInit = strToHTML(res)

  const tableBody = htmlInit.querySelector('#DefaultContent_gvOrders>tbody')
  let rows = Array.from(tableBody.children)

  rows.shift()
  
  return {
    rows,
    __VIEWSTATE: htmlInit.querySelector('#__VIEWSTATE').value,
    __VIEWSTATEGENERATOR: htmlInit.querySelector('#__VIEWSTATEGENERATOR').value,
    __EVENTVALIDATION: htmlInit.querySelector('#__EVENTVALIDATION').value,
  }
}
