<script>
import Queue from './templates/Queue.svelte'
import SearchCard from './templates/SearchCard.svelte'
import ExperimentalCard from './templates/ExperimentalCard.svelte'

import { onMount } from 'svelte'
import { showGridTemplate, hideGridTemplate, makeNonResizableDiv, makeResizableDiv} from './utils/resize.js'
import { toast } from '@brocococonut/toast.js/toast.mjs'

const rootEl = [...document.getElementsByTagName('html')][0]
const storage = window.localStorage

let init = true
let counter = 1
let resizing = false

let queues = JSON.parse(storage.getItem('config') || '[]')
let opts = JSON.parse(storage.getItem('opts') || '{}')

const refresher = async () => {
  await fetch('https://internal.softlayer.com/Hardware/index?output=json')
  console.debug('IMS session refreshed')
}

const openOptionsPage = () => {
  let ctx = null
  if (chrome !== undefined) ctx = chrome // eslint-disable-line
  else toast("Who knows what browser this is ¯\\_(ツ)_/¯\nIt's obviously not chrome or firefox, so there's likely gonna be an error in the console (╯°□°）╯︵ ┻━┻", 4000, 'error')

  if (ctx) ctx.runtime.openOptionsPage()
}

const resizeQueues = (ev) => {
  resizing = !resizing
  if (resizing) {
    showGridTemplate()
    queues.map(queue => makeResizableDiv(queue.el))
  } else {
    if (ev.shiftKey) {
      let tempQueues = queues.map(queue => {
        const el = queue.el
        const rowStart = el.getAttribute('rs-row')
        const rowSpan = el.getAttribute('rs-row-span')
        const colStart = el.getAttribute('rs-col')
        const colSpan = el.getAttribute('rs-col-span')

        if (rowStart && rowSpan && colStart && colSpan) {
          queue.rowStart = rowStart
          queue.rowSpan = rowSpan
          queue.colStart = colStart
          queue.colSpan = colSpan
        }

        return queue
      })
      .map(a => ({...a}))
      .map(queue => {
        delete queue.app
        delete queue.el
        return queue
      })
      
      storage.setItem('config', JSON.stringify(tempQueues))
      toast('Your queue layout has been saved - it will persist through refreshes', 5000, 'success')
    }
    hideGridTemplate()
    queues.map(queue => makeNonResizableDiv(queue.el))
  }
}

onMount(() => {
  refresher()
  setInterval(refresher, 10*60*1000)

  document.body.addEventListener('keydown', ev => {
    if (ev.key === 'Escape') {
      new Array(...document.getElementsByClassName('lsfs')).map(el => {
        const classList = el.className.split(' ')
        classList.splice(classList.indexOf('lsfs'), 1)
        el.className = classList.join(' ')
      })
    } else if (ev.altKey && ev.key === 'o') {
      openOptionsPage()
    }
    else if (ev.altKey && (ev.key === 'r' || ev.key === 'R')) {
      resizeQueues(ev)
    }
  })

  chrome.contextMenus.removeAll()
  chrome.contextMenus.create({
    id: "dash-resize-queues",
    title: 'Resize Queues',
    contexts: ["all"],
    documentUrlPatterns: [document.URL]
  }, () => {});
  chrome.contextMenus.create({
    id: "dash-open-options",
    title: 'Open Options',
    contexts: ["all"],
    documentUrlPatterns: [document.URL]
  }, () => {});


  chrome.contextMenus.onClicked.addListener((info, tab) => {
    switch (info.menuItemId) {
      case 'dash-resize-queues':
        resizeQueues({shiftKey: false})
        break
      case 'dash-open-options':
        openOptionsPage()
        break
    } 
  })
})
</script>

<main class="{opts.dark ? 'dark' : ''}">
  <div class="grid-container">
    <div class="grid">
      {#each queues as queue}
        <Queue queue={queue}></Queue>
      {/each}
    </div>
  </div>
  {#if opts.showSearch}
    <div class="grid-container" style="height:auto;">
      <div class="grid" style="grid-template-rows: 1fr">
        <SearchCard />
      </div>
    </div>
  {/if}
  {#if opts.showExp}
  <div class="grid-container" style="height:auto;">
    <div class="grid" style="grid-template-rows: 1fr">
      <ExperimentalCard />
    </div>
  </div>
  {/if}
</main>