<script>
import { getDetails } from '../utils/ims.js'

let val = ''

const handleKeyDown = ev => {
  if (ev.key === "Enter" && val !== "") {
    getDetails(val);
  }
}
</script>

<div class="card" style="grid-column:1/span 6;grid-row:1/span 1">
  <div class="card-header">
    <span class="h3 lal">Search</span>
    <i><small class="h3 lar">Enter 1+ of the following: TXN Link, HWO Link, Ticket Link, SLTag</small></i>
  </div>
  <div class="card-body">
    <input type="text" on:keydown={handleKeyDown} bind:value={val} style="width: 100%;height: 36px;border: none;border-radius: 8px;padding: 0 8px;box-shadow: inset rgba(33,33,33,0.5) 0px 0 3px">
  </div>
</div>
