import { toast } from '@brocococonut/toast.js/toast.mjs'
import { SLAPIRequest, getCookie } from './api'

export const getDetails = inp => {
  const _dispatch = 'https://internal.softlayer.com/dispatch'

  const HWOLinkReg = /(?:https:\/\/internal.softlayer.com)?\/Hardware\/(?:view|update)\/([0-9]{0,10})/gi
  const searchSLTagReg = /(sl[0-9a-z]{3,12})/gi
  const ticketReg = /https:\/\/internal\.softlayer\.com\/Ticket\/(?:ticketPreview|ticketEdit)\/([0-9]{1,10})/gi
  const TXNReg = new RegExp(
    '(?:https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/)([0-9]{1,12})(?:/1)?',
    'gi'
  )

  const ticketCorrelation = {}
  const TXNCorrelation = {}

  /**
   * Parse an initial input from a user using REGEX
   * and return an URL query partial
   * @param   {String}    str Initial input
   * @returns {Promise}       Returns a promise containing an array
   *                          of URL query partials
   */
  const parseStr = (str = '', res = []) => new Promise(async (resolve, reject) => {
    try {
      let tickets = str.match(ticketReg) || []
      let TXNLinks = str.match(TXNReg) || []
      let SLTags = str.match(searchSLTagReg) || []
      let HWSerials = str.match(HWOLinkReg) || []

      toast(`${tickets.length} ticket/s entered`)
      toast(`${SLTags.length} SLTag/s entered`)
      toast(`${HWSerials.length} HWO/s entered`)
      toast(`${TXNLinks.length} TXN/s entered`)

      tickets = await Promise.all(tickets.map(checkTicket))
      TXNLinks = await Promise.all(TXNLinks.map(checkTXN))
      HWSerials = [...new Set([...HWSerials, ...tickets, ...TXNLinks])]
      SLTags = SLTags.filter(tag => {
        const lowerTag = tag.toLocaleLowerCase()
        return lowerTag !== 'slackbot' && lowerTag !== 'slack' && lowerTag !== 'slightly'
      })

      if (
        tickets.length < 1 &&
        HWSerials.length < 1 &&
        SLTags.length < 1 &&
        TXNLinks.length < 1
      ) {
        toast('Nothing valid to search')
        return reject(new ReferenceError('Search query invalid'))
      }

      const SLTagsStr = SLTags.join('+')
      const HWSerialsStr = HWSerials.map(hwolink => {
        if (typeof hwolink === 'string') return hwolink.split('/').pop()
        else return hwolink
      }).join('+')

      if (SLTagsStr !== '') { res.push(`&data[Hardware][search][serialNumbers]=${SLTagsStr}`) }
      if (HWSerialsStr !== '') { res.push(`&data[Hardware][search][id]=${HWSerialsStr}`) }

      resolve(res)
    } catch (err) {
      console.error(err)
      reject(err)
    }
  })

  /**
   * Get the attached HWO/s from a specified ticket
   * @param {String} str Ticket url
   * @returns {Array} Array of SLTags
   */
  const checkTicket = (str = '') => new Promise(async resolve => {
    const id = str.split('/').pop()

    try {
      toast(`Checking ticket ${id}`)

      let res = await window.fetch(
        `https://internal.softlayer.com/Ticket/ticketPreviewGetAttachedHardware/${id}?output=json`
      )
      res = await res.json()

      const {
        SoftLayer_Controller_Internal_Ticket: {
          ticket: {
            attachedHardware
          }
        }
      } = res

      const hwos = objToArr(attachedHardware).map(hwo => hwo.id)

      toast(`${hwos.length} HWO/s found attached to the ticket ${id}`)

      if (hwos.length > 0) {
        ticketCorrelation[id] = []
      }

      hwos.map(hwid => {
        ticketCorrelation[id].push(hwid)
      })

      resolve(hwos)
    } catch (err) {
      console.error(err)
      toast(
        `An error occurred getting linked HWOs for ticket ${id}. An error has been logged to the console`,
        8000,
        'error'
      )
      resolve([])
    }
  })

  const checkTXN = (str = '') => new Promise(async resolve => {
    const TXNRegXtra = new RegExp(
      '(?:https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/)([0-9]{1,12})(?:/[0-9])?',
      'gi'
    )
    var cache = []
    var id = TXNRegXtra.exec(str) || []
    id = id[1] || null

    if (id === null) return resolve([])

    try {
      toast(`Checking TXN ${id}`)

      var res = await window.fetch(
        `${_dispatch}/HardwareTransaction/viewTransactionDetails/${id}?output=json`
      )
      res = await res.json()

      const {
        SoftLayer_Controller_Internal_HardwareTransaction: {
          transaction: {
            hardwareId
          }
        }
      } = res
      if (hardwareId) {
        if (cache.indexOf(hardwareId) === -1) {
          cache.push(hardwareId)
        }

        toast(`1 HWO/s found attached to the TXN ${id}`)

        if (!TXNCorrelation[id]) TXNCorrelation[id] = []

        TXNCorrelation[id].push(hardwareId)
      }

      resolve(hardwareId)
    } catch (err) {
      console.error(err)
      toast(
        `An error occurred getting linked HWOs for TXN ${id}. An error has been logged to the console`,
        8000,
        'error'
      )
      resolve([])
    }
  })

  /**
   * Pads numbers to be 5 in length for sorting purposes
   * @param {String} intake Location string
   */
  const numberFixer = intake => {
    var str = intake.replace(/([0-9]+)/gi, '')
    var num = intake.replace(/([a-z]+)/gi, '')
    num = `${num}`.padStart(5, '0')

    return `${str}${num}`
  }

  const objToArr = obj => Object.values(obj)

  new Promise(async resolve => {
    try {
      // Parse user input
      let query = await parseStr(inp)

      query = query.join('')

      // Fetch the details for all the SLTags and HW Ids found from the
      // user input
      let req = await window.fetch(
        `${_dispatch}/Hardware/searchResults/?paginationOffset=0&paginationLimit=700&output=json${query}`
      )
      req = await req.json()

      const {
        SoftLayer_Controller_Internal_Hardware: {
          hardwareList = null
        }
      } = req

      let hwos = []

      if (hardwareList) hwos = objToArr(hardwareList)

      // Get the location, sltag, hwid, and tickets for each row
      let results = hwos.map((hwo = {}) => {
        const location = hwo.locationPathString
        const sltag = hwo.serialNumber || 'none'
        const hwid = hwo.id || 'none'
        const tickets = []
        const TXNs = []

        for (const key in ticketCorrelation) {
          if (ticketCorrelation.hasOwnProperty(key)) {
            const arr = ticketCorrelation[key]
            if (arr.indexOf(hwid) > -1) tickets.push(key)
          }
        }
        for (const key in TXNCorrelation) {
          if (TXNCorrelation.hasOwnProperty(key)) {
            const arr = TXNCorrelation[key]
            if (arr.indexOf(hwid) > -1) TXNs.push(key)
          }
        }

        return {
          tickets: tickets
            .map(
              id =>
                `<a href="https://internal.softlayer.com/Ticket/ticketPreview/${id}">${id}</a>`
            )
            .join(' | '),
          txns: TXNs.map(
            id =>
              `<a href="https://internal.softlayer.com/HardwareTransaction/viewTransactionDetails/${id}/1">${id}</a>`
          ).join(' | '),
          location: location,
          sltag: sltag,
          hwid
        }
      })

      const idMap = results.map(obj => obj.hwid)

      // Make the array unique
      results = results.filter((val, i) => idMap.indexOf(val.hwid) === i)

      results = results
        .sort(({ location: lA }, { location: lB }) => {
          lA = lA.split('.').map(numberFixer)
          lB = lB.split('.').map(numberFixer)

          return lA[3] < lB[3] ? -1 : lA[3] > lB[3] ? 1 : 0
        })
        .sort(({ location: lA }, { location: lB }) => {
          lA = lA.split('.').map(numberFixer)
          lB = lB.split('.').map(numberFixer)

          return lA[2] < lB[2] ? -1 : lA[2] > lB[2] ? 1 : 0
        })
        .sort(({ location: lA }, { location: lB }) => {
          lA = lA.split('.').map(numberFixer)
          lB = lB.split('.').map(numberFixer)

          return lA[1] < lB[1] ? -1 : lA[1] > lB[1] ? 1 : 0
        })
        .sort(({ location: lA }, { location: lB }) => {
          lA = lA.split('.').map(numberFixer)
          lB = lB.split('.').map(numberFixer)

          return lA[0] < lB[0] ? -1 : lA[0] > lB[0] ? 1 : 0
        })
        .sort(({ tickets: tA }, { tickets: tB }) =>
          tA < tB ? -1 : tA > tB ? 1 : 0
        )
        .map(
          o =>
            `<tr><td>${o.txns}</td><td>${o.tickets}</td><td>${
            o.location
            }</td><td>${
            o.sltag
            }</td><td><a target="_blank" href="https://internal.softlayer.com/Hardware/update/${
            o.hwid
            }?layout=minimal">${o.hwid}</a></td></tr>`
        )
        .join('')

      // Create a new blob and Object URL to open in a new page
      const css =
        'table,td,th{border:1px solid #000;padding-left:5px;padding-right:5px;border-collapse:collapse}th{border-color:#f0f0f0;background-color:#222;color:#f0f0f0}tr:nth-of-type(2n){background-color:#efefef}'
      const blob = URL.createObjectURL(
        new window.Blob(
          [
            `<style>${css}</style><pre><table><tr><th>TXN/s</th><th>Ticket/s</th><th>Location</th><th>SLTag</th><th>HWID</th></tr>${results}</table></pre>`
          ],
          {
            type: 'text/html'
          }
        )
      )

      window.open(blob, '_blank')

      setTimeout(() => URL.revokeObjectURL(blob), 10 * 60 * 1000)
    } catch (err) {
      toast(
        'An error occurred that caused the search to fail; it has been logged in the console. Please share these details with the creator',
        8000,
        'error'
      )
      console.error(err)
    } finally {
      resolve()
    }
  }).catch(console.error)
}

export const checkLiquidation = async () => {
  const input = window.prompt('Please enter SLTags (other text will be removed)', '')

  const parseStr = (str = '', res = []) => {
    const searchSLTagReg = /(sl[0-9a-z]{3,12})/gi

    let SLTags = str.match(searchSLTagReg) || []

    toast(`${SLTags.length} SLTag/s entered`)

    SLTags = SLTags.filter(tag => {
      const lowerTag = tag.toLocaleLowerCase()
      return lowerTag !== 'slackbot' && lowerTag !== 'slack' && lowerTag !== 'slightly'
    })

    if (SLTags.length < 1) {
      toast('Nothing valid to search')
      throw new ReferenceError('Search query invalid')
    }

    return SLTags
  }

  const SLTags = parseStr(input)

  const api = new SLAPIRequest({
    service: 'SoftLayer_Hardware',
    func: 'getAllObjects',
    filter: {
      serialNumber: {
        operation: 'in',
        options: [{
          name: 'data',
          value: SLTags
        }]
      }
    },
    username: await getCookie('ims_int_portal_login'),
    password: await getCookie('ims_int_portal_hash')
  })

  api.limit(40).mask.set({
    id: {},
    serialNumber: {},
    hardwareStatus: {
      id: {},
      keyName: {}
    },
    locationPathString: {},
    networkComponents: {
      id: {},
      networkHardware: {
        id: {}
      }
    },
    powerComponents: {
      id: {},
      uplink: {
        id: {}
      }
    },
    internalNotesHistory: {
      id: {},
      note: {},
      employee: {
        id: {},
        firstName: {},
        lastName: {}
      }
    }
  })

  const res = await api.getNumOfPages(Math.ceil(SLTags.length / 40))

  const rows = res.map(obj => {
    return `<tr><td><a href="https://internal.softlayer.com/Hardware/update/${obj.id}?layout=minimal">${
      obj.id
    }</a></td><td>${
      obj.serialNumber
    }</td><td>${
      obj.hardwareStatus.keyName
    }</td><td>${
      obj.locationPathString
    }</td><td>${
      obj.powerComponents.filter(comp => comp.uplink !== undefined).length > 0 ? 'Connected' : 'Disconnected'
    }</td><td>${
      obj.networkComponents.filter(comp => comp.networkHardware !== undefined).length > 0 ? 'Connected' : 'Disconnected'
    }</td><td>${
      obj.internalNotesHistory[0] ? `${
        obj.internalNotesHistory[0].employee.firstName
      } ${
        obj.internalNotesHistory[0].employee.lastName
      }</td><td>${
        obj.internalNotesHistory[0].note
      }` : '</td><td>No notes'
    }</td></tr>`
  })

  const css =
        'table,td,th{border:1px solid #000;padding-left:5px;padding-right:5px;border-collapse:collapse}th{border-color:#f0f0f0;background-color:#222;color:#f0f0f0}tr:nth-of-type(2n){background-color:#efefef}'
  const blob = URL.createObjectURL(
    new window.Blob(
      [
        `<style>${css}</style><pre><table><tr><th>id</th><th>SLTag</th><th>Status</th><th>Location</th><th>Power Status</th><th>Network Status</th><th>Note By</th><th>Note</th></tr>${rows.join('')}</table></pre>`
      ],
      {
        type: 'text/html'
      }
    )
  )

  window.open(blob, '_blank')

  setTimeout(() => URL.revokeObjectURL(blob), 10 * 60 * 1000)
}
